<template>
  <div>
    <Bar :chart-data="chartData"/>
  </div>
</template>
<script>
import axios from 'axios'
import Bar from "@/components/Chart/Bar";
export default {
  components: {
    Bar
  },
  mounted() {
      let fd = new FormData()
      fd.append('data', `select * from sekolah_nilai where id_siswa='${this.$store.state.users.id_siswa}'`)//database setting di server
      fd.append('database','infolay3_test')
      axios.post('https://infolayanans.space/api/mysql/auto.php', fd
      , {
          headers: {
          Authorization: localStorage.getItem('auth._token.local')
          }
      }).then(res => {
          console.log(res)
          this.$forceUpdate()
      })
  },
  data() {
    return {
      chartData: {
        labels: [
          "January",
          "February",
          "March",
          "April",
          "May",
          "June",
          "July",
          "August",
          "September",
          "October",
          "November",
          "December"
        ],
        datasets: [
          {
            label: "Data One",
            backgroundColor: "green",
            data: [40, 20, 40, 50, 20, 60, 70, 40, 50, 60, 20, 10]
          }
        ]
      }
    };
  }
};
</script>